/**
 * @fileOverview
 * This file contains all the TypeScript types and Zod schemas for the AI flows.
 * Separating these from the flow logic allows them to be used across the application
 * without pulling in server-side dependencies.
 */

import {z} from 'zod';

// Schema for a single keyword
export const KeywordSchema = z.object({
  id: z.string().describe('A unique identifier for the keyword, e.g., "w1".'),
  name: z.string().describe('The trending keyword phrase.'),
  volume: z.number().optional().describe('The estimated search volume or trend score for the keyword.'),
  change: z.number().describe('The percentage change in trend score (e.g., week-over-week). Can be positive or negative.'),
  difficulty: z.number().optional().describe('An estimated SEO difficulty score from 0 to 100.'),
  serpFeatures: z.array(z.string()).optional().describe('A list of observed SERP features, like "Featured Snippet" or "Image Pack".'),
  sources: z.array(z.string()).optional().describe('A list of simulated data sources used for this keyword trend, e.g., "Google Trends", "Exploding Topics".')
});
export type Keyword = z.infer<typeof KeywordSchema>;


// Schema for the output of the trending keywords generation flow
export const GenerateTrendingKeywordsOutputSchema = z.object({
  hour: z.array(KeywordSchema).describe('Trending keywords for the past hour.'),
  day: z.array(KeywordSchema).describe('Trending keywords for the past day.'),
  week: z.array(KeywordSchema).describe('Trending keywords for the past week.'),
  month: z.array(KeywordSchema).describe('Trending keywords for the past month.'),
});
export type GenerateTrendingKeywordsOutput = z.infer<typeof GenerateTrendingKeywordsOutputSchema>;

// Schema for the input of the trending keywords generation flow
export const GenerateTrendingKeywordsInputSchema = z.object({
  businessType: z.string().describe('The user-provided business type or industry, e.g., "SaaS for project management".'),
  country: z.string().describe('The target country for the analysis, e.g., "United States".'),
  city: z.string().optional().describe('The optional target city for a more localized analysis, e.g., "San Francisco".'),
});
export type GenerateTrendingKeywordsInput = z.infer<typeof GenerateTrendingKeywordsInputSchema>;


// Schema for the Arabic translation flow input
export const TranslateKeywordsArabicInputSchema = z.object({
  businessType: z.string().describe('The business context for the translation.'),
  keywords: z.string().describe('A comma-separated string of English keywords to be translated.'),
});
export type TranslateKeywordsArabicInput = z.infer<typeof TranslateKeywordsArabicInputSchema>;

// Schema for a single translated keyword
export const TranslatedKeywordSchema = z.object({
  keyword: z.string().describe('The keyword translated into Arabic.'),
  volume: z.number().describe('The estimated search volume for the keyword in the KSA market.'),
  change: z.number().describe('The estimated trend change percentage for the keyword.'),
});

// Schema for the Arabic translation flow output
export const TranslateKeywordsArabicOutputSchema = z.object({
  translatedKeywords: z.array(TranslatedKeywordSchema),
});
export type TranslateKeywordsArabicOutput = z.infer<typeof TranslateKeywordsArabicOutputSchema>;


// Schema for SEO content suggestions input
export const SeoContentSuggestionsInputSchema = z.object({
  businessType: z.string().describe('The business context for the content.'),
  trendingKeywords: z.string().describe('A comma-separated list of trending keywords to base suggestions on.'),
});
export type SeoContentSuggestionsInput = z.infer<typeof SeoContentSuggestionsInputSchema>;

// Schema for a single SEO content suggestion (brief)
export const SuggestionSchema = z.object({
    title: z.string().describe("A compelling, SEO-friendly title for a piece of content."),
    hook: z.string().describe("An engaging opening sentence or question to capture the reader's attention."),
    points: z.array(z.string()).describe("A list of key talking points or sections to cover in the content.")
});

// Schema for SEO content suggestions output
export const SeoContentSuggestionsOutputSchema = z.object({
  suggestions: z.array(SuggestionSchema).describe('A list of generated content briefs.'),
});
export type SeoContentSuggestionsOutput = z.infer<typeof SeoContentSuggestionsOutputSchema>;


// Schema for advanced SEO keyword analysis input
export const AdvancedSeoKeywordAnalysisInputSchema = z.object({
  businessType: z.string().describe('The user-provided business type or industry.'),
  keyword: z.string().describe('The specific keyword to perform a deep analysis on.'),
});
export type AdvancedSeoKeywordAnalysisInput = z.infer<typeof AdvancedSeoKeywordAnalysisInputSchema>;

// Schema for advanced SEO keyword analysis output
export const AdvancedSeoKeywordAnalysisOutputSchema = z.object({
  searchIntent: z.string().describe('The primary user intent behind the keyword (e.g., Informational, Navigational, Transactional, Commercial Investigation).'),
  targetAudience: z.string().describe('A description of the ideal audience for content targeting this keyword.'),
  competitiveLandscape: z.string().describe('A brief analysis of the current top-ranking content and competition level.'),
  contentAngle: z.string().describe('A unique angle or perspective to make content stand out.'),
  longTailKeywords: z.array(z.string()).describe('A list of related long-tail keywords.'),
  relatedQuestions: z.array(z.string()).describe('A list of common questions users ask related to this keyword.'),
  detailedContentOutline: z.object({
      title: z.string().describe("A proposed, fully-formed title for the content piece."),
      sections: z.array(z.object({
          heading: z.string().describe("The heading for a section of the content."),
          points: z.array(z.string()).describe("Bullet points to be covered within that section.")
      })).describe("The sections that should make up the content.")
  }).describe("A detailed outline for a blog post or article."),
  difficultyAnalysis: z.string().describe('A qualitative analysis of why this keyword is easy or difficult to rank for.'),
  confidenceScore: z.number().describe('The AI\'s confidence in this analysis, from 0 to 100.'),
  simulatedSources: z.array(z.string()).describe('A list of simulated data sources used for the analysis.'),
});
export type AdvancedSeoKeywordAnalysisOutput = z.infer<typeof AdvancedSeoKeywordAnalysisOutputSchema>;


// Schema for chart takeaway generation input
export const GenerateChartTakeawayInputSchema = z.object({
    businessType: z.string().describe("The business context for the chart analysis."),
    keywordChartData: z.string().describe("A JSON string representing the data plotted on the chart. e.g., '[{\"name\":\"AI tools\",\"volume\":12000}, ...]'")
});
export type GenerateChartTakeawayInput = z.infer<typeof GenerateChartTakeawayInputSchema>;

// Schema for chart takeaway generation output
export const GenerateChartTakeawayOutputSchema = z.object({
    takeaway: z.string().describe("A single, concise, CMO-level sentence summarizing the key insight from the chart data.")
});
export type GenerateChartTakeawayOutput = z.infer<typeof GenerateChartTakeawayOutputSchema>;

// Schema for Market Deep-Dive input
export const MarketDeepDiveInputSchema = z.object({
    businessType: z.string().describe("The user-provided business type or industry."),
    country: z.string().describe("The target country for the analysis."),
    city: z.string().optional().describe("The optional target city for a more localized analysis.")
});
export type MarketDeepDiveInput = z.infer<typeof MarketDeepDiveInputSchema>;

// Schema for Market Sizing metric
const MarketMetricSchema = z.object({
    value: z.number().describe("The estimated market value in USD."),
    description: z.string().describe("A brief explanation of what this metric represents."),
    sources: z.array(z.string()).describe("A list of simulated data sources for this estimation.")
});

// Schema for Competitor analysis
const CompetitorSchema = z.object({
    name: z.string().describe("The name of the competitor."),
    strengths: z.string().describe("Key strengths of the competitor."),
    weaknesses: z.string().describe("Key weaknesses of the competitor."),
    marketShare: z.string().describe("Estimated market share percentage (e.g., '15%').")
});

// Schema for SWOT analysis
const SwotSchema = z.object({
    strengths: z.array(z.string()).describe("Internal strengths for the business in this market."),
    weaknesses: z.array(z.string()).describe("Internal weaknesses for the business in this market."),
    opportunities: z.array(z.string()).describe("External opportunities in the market."),
    threats: z.array(z.string()).describe("External threats in the market.")
});

// Schema for Market Deep-Dive output
export const MarketDeepDiveOutputSchema = z.object({
    executiveSummary: z.string().describe("A high-level summary of the entire market analysis for a C-suite executive."),
    tamSamSom: z.object({
        tam: MarketMetricSchema.describe("Total Addressable Market"),
        sam: MarketMetricSchema.describe("Serviceable Addressable Market"),
        som: MarketMetricSchema.describe("Serviceable Obtainable Market")
    }).describe("Market Sizing analysis."),
    competitors: z.array(CompetitorSchema).describe("A benchmark of key competitors."),
    swot: SwotSchema.describe("A SWOT analysis for the user's business type in this market.")
});
export type MarketDeepDiveOutput = z.infer<typeof MarketDeepDiveOutputSchema>;
